@import "../base/variables";
@import "../base/font";

.start-page {
  height: 100vh;
  background-color: $main-background-color-and-button-text;
  color: $main-text-color;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;

  h1 {
    font-size: 3.5rem;
    font-family: "Karla";
  }

  h3 {
    font-weight: normal;
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
}
